/* Customize your main colors in :root variables */
:root {
  --main-background-color: #343A50;
  --card-background-color: #51576D;
  --card-background-lighter-color: #7A7F92;
  --main-text-color:#F7F6F4;
  --title-text-color:#ffffff;
}

body{
  background: url("img/bgbig.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  /*background-color:var(--main-background-color);*/
  background-size: cover;

  font-family: 'Arial', cursive;
  font-size: 1.1em;
}

h2, p{
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}

.MuiPaper-elevation3 {
box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 0%), 0px 3px 4px 0px rgb(0 0 0 / 0%), 0px 1px 8px 0px rgb(0 0 0 / 0%) !important;
}

